<script>
import { mapState, mapActions, mapMutations } from "vuex";
import { DatePicker } from 'v-calendar';
export default {
  name: "App",
  data() {
    return {
      dateTime: null,
      atts:[{
        key: this.dateTime,
        dates: new Date(),
      }],
      modelConfig: {
        type: 'string',
        mask: 'YYYY-MM-DD h:mm:A', // Uses 'iso' if missing
      },
      // v-calendar
      dateShow:false,

      // Rules
      numberRule:[
        v => (!isNaN(parseFloat(v)) && v >= 0 && v <= 99999999999999) || 'Only Number Needed'
      ],
      user_nm_rule: [
        v => !!v || '이름은 필수 입력사항입니다.',
        v => !(v && v.length >= 30) || '이름은 30자 이상 입력할 수 없습니다.',
        v => !/[~!@#$%^&*()_+|<>?:{}]/.test(v) || '이름에는 특수문자를 사용할 수 없습니다.'
      ],


    }
  },
  computed: {
    ...mapState(["user", "content", "profile"]),
  },
  components: {
    DatePicker,
  },
  methods: {
    ...mapMutations(["remove_user_status"]),
    ...mapActions(["user_verify"]),


    handleClick(){
      console.log("------------------------------")
    },
    async submitForm() {
      this.profile.user_profile.process_step = 4
      this.$store.dispatch("profile/setProfile", {
        id: this.profile.user_profile.id,
        data: this.profile.user_profile,
      });
    },
  },
};
</script>
